
body {
  margin: 0;
  /* overflow-y: hidden; */
}

/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey;
  /* border-radius: 10px; */
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: crimson;
  /* border-radius: 5px; */
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
  background: rgb(219, 47, 81);
}


@media only screen and (max-width: 425px) {

  .JoinPage {
    display: flex;
    justify-content: center;
    align-items: flex-start !important;
  }

  .JoinContainer {
    margin-top: 30vw !important;
    width: 100% !important;

  }

  .JoinContainer>img {
    width: 30vw !important;
  }

  .JoinContainer>h1 {
    font-size: 6vw !important;
    width: 65vw !important;
    border-bottom: 0.6vw solid white !important;
    padding: 3vw !important;
  }

  #joinInput {
    width: 65vw !important;
    padding: 4vw !important;
    font-size: 5vw !important;
    margin: 5vw !important;
  }

  .joinbtn {
    width: 65vw !important;
    padding: 4vw !important;
    font-size: 5vw !important;
  }

  .chatPage {
    width: 100%;
  }

  .chatContainer {
    background-color: white;
    width: 90% !important;
    height: 75% !important;
  }

  .header {
    height: 10% !important;
  }

  .header>h2 {
    margin: 3vw !important;
    font-size: 5vw !important;
  }

  .header>a>img {
    margin: 5vw !important;
    -webkit-transform: scale(2) !important;
            transform: scale(2) !important;
  }

  .header>a>img:hover {
    -webkit-transform: scale(3.2) !important;
            transform: scale(3.2) !important;
  }

  .chatBox {
    height: 80% !important;
  }

  .inputBox {
    height: 10% !important;
  }

  #chatInput {
    padding: 4vw !important;
    font-size: 4vw !important;
  }

  .messageBox {
    padding: 3vw !important;
    margin: 3vw !important;
    border-radius: 1.5vw !important;
    font-size: 3.7vw !important;
  }
}

@media only screen and (max-width: 700px ) and (min-width : 425px) {


  .JoinPage {
    display: flex;
    justify-content: center;
    align-items: flex-start !important;
  }

  .JoinContainer {
    margin-top: 15vw !important;
    width: 65% !important;
  }

  .JoinContainer>img {
    width: 20vw !important;
  }

  .JoinContainer>h1 {
    font-size: 3vw !important;
    width: 50vw !important;
    border-bottom: 0.6vw solid white !important;
    padding: 3vw !important;
  }

  #joinInput {
    width: 50vw !important;
    padding: 4vw !important;
    font-size: 3vw !important;
    margin: 2vw !important;
    height: 2vh;
  }

  .joinbtn {
    width: 50vw !important;
    padding: 2vw !important;
    font-size: 3vw !important;
    /* height: 2vh; */
  }

  .chatPage {
    width: 100%;
  }

  .chatContainer {
    background-color: white;
    width: 80% !important;
    height: 68% !important;
  }

  .header {
    height: 13% !important;
  }

  .header>h2 {
    margin: 1vw !important;
    font-size: 3vw !important;
  }

  .header>a>img {
    margin: 1.5vw !important;
    -webkit-transform: scale(2) !important;
            transform: scale(2) !important;
  }

  .header>a>img:hover {
    -webkit-transform: scale(3.2) !important;
            transform: scale(3.2) !important;
  }

  .chatBox {
    height: 74% !important;
  }

  .inputBox {
    height: 13% !important;
  }

  #chatInput {
    font-size: 2vw !important;
  }

  .messageBox {
    padding: 1vw !important;
    margin: 1vw !important;
    border-radius: 1.5vw !important;
    font-size: 2vw !important;
  }
}
.JoinPage{
  background-color: rgb(22, 21, 21);
  width : 100vm;
  height : 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.JoinContainer{
  /* border: 2px solid white; */
  width : 50%;
  height : 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.JoinContainer > img{
  width : 10vmax;
}

.JoinContainer > h1{
  color: white;
  font-size: 2.5vmax;
  width : 310px;
  border-bottom: 0.2vmax solid white;
  font-family: 'Roboto', sans-serif;
  text-align: center;
  padding: 1vmax;
}

#joinInput{
  width: 25vmax;
  padding: 1vmax;
  border: none;
  outline: none;
  font-size: 1.2vmax;
  margin: 2vmax;
  box-sizing: border-box;
}

.joinbtn{
  width: 25vmax;
  padding: 1vmax;
  border: none;
  color: white;
  font-family: "Roboto";
  outline: 1.2vmax;
  background-color: rgb(248, 8, 56);
  box-sizing: border-box;
  transition: all 0.3s;
  cursor: pointer;
}
.joinbtn:hover{
  background-color: rgb(197, 25, 59);
}
.chatPage{
  background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAUAAAAFCAYAAACNbyblAAAAHUlEQVQIW2NgY2OzYUACYL6+vn4UsgAynwwBEB8ARuIGpsZxGOoAAAAASUVORK5CYII=);
  background-color: rgb(22, 21, 21);
  width : 100vw;
  height : 100vh;
  display: flex;
  justify-content:center;
  align-items: center;
}

.chatContainer{
  border-radius: 1vmax 1vmax;
  overflow: hidden;
  box-shadow: 0 4px 8px 0 rgba(129, 126, 126, 0.2), 0 6px 20px 0 rgba(88, 64, 64, 0.19);
  background-color: white;
  height: 60%;
  width: 50%;
}

.header{
  background-color: crimson;
  height: 15%;
  display: flex;
  justify-content: space-between;
}
.header > h2{
  color : white;
  margin: 1.2vmax;
}
.header > a > img{
  width: 0.9vmax;
  height: 0.9vmax;
  margin: 1.9vmax;
  transition: all 0.3s;
  cursor: pointer;
}
.header > a > img:hover{
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
}

.chatBox{
  border-bottom: 0.2vmax solid black;
  height: 70%;
  box-sizing: border-box;
}

.inputBox{
  height: 15%;
  box-sizing: border-box;
  display: flex;
}

#chatInput{
  width: 80%;
  border: none;
  padding: 2vmax;
  box-sizing: border-box;
  background-color: rgb(255, 255, 255);
  outline: none;
  font-size: 1.5vmax;
  font-weight: 500;
  font-family: "Roboto";
}

.sendBtn{
  background-color: rgb(255, 1, 52);
  color: white;
  border: none;
  width: 21%;
  transition: all 0.3s;
  cursor: pointer;
  font-size: 1.2vmax;
}
.sendBtn:hover{
  background-color: rgb(151, 8, 8);
}
.sendBtn > img{
  width: 3vmax;
  -webkit-filter: brightness(100) invert();
          filter: brightness(100) invert();
  transition: all 0.3s;
}
.sendBtn:hover img {
  -webkit-transform: translateX(10px);
          transform: translateX(10px);
}


.messageBox{
  /* max-width: 80%; */
  padding: 1vmax;
  margin: 1vmax;
  border-radius: 0.5vmax;
  display: inline-block;
  clear: both;
  font-size: 1.2vmax;
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  transition: all 0.8s;
}


.right{
  float: right;
  text-align: right;
  background-color: rgb(7, 189, 240);
  color: white;
 
}

.left{
  float: left;
  text-align: left;
  background-color: rgb(41, 190, 61);
  color: white;
}

.right:hover{
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
}
.left:hover{
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
}

.middle{
  display: flex;
  justify-content: center;
  text-align: center;
  background-color: rgb(196, 255, 184);
}
.alert{
  top: 10%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  max-width: 100%;
  position: fixed;
  z-index: 9;
}
